/** @format */
section.PaymentFailure {
  width: 100%;
  height: 100vh;
  font-family: Roboto, sans-serif;
  padding-bottom: 50px;
}

.Payment_Logo_Section {
  text-align: center;
  padding-top: 30px;
  width: 100%;
  height: 45vh;
  background-color: #f4f6f8;
  position: relative;
}

.Payment_Logo_Section div H1 {
  margin-bottom: 20px;
}

.Payment_Logo_Section div {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: auto;
}

.Payment_Message {
  padding: 30px 20px 30px 20px;
  text-align: center;
  margin: auto;
  width: 90%;
}

.Payment_Message h1 {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.42;
  color: #24272c;
}

.Payment_Message p {
  padding: 10px 20px 15px 20px;
  margin-top: 10px;
  font-size: 16px;
  line-height: 1.47;
  color: #566171;
}

.Payment_Message button {
  padding: 20px;
  margin: 10px auto;
  letter-spacing: 1px;
}

.Purchase_Details {
  position: relative;
  top: 30px;
  width: 85%;
  margin: auto;
  color: #566171;
}

.Purchase_Details h1 {
  font-size: 16px;
  font-weight: 500;
}

.Purchases {
  margin-bottom: 25px;
}

.Purchases div {
  position: relative;
  width: 90%;
  margin: 5px auto;
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 15px 0 15px 0;
}

.Purchases div::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  opacity: 0.15;
  background-color: #24272c;
}

.Purchases div p:nth-child(2) {
  text-align: end;
}

.Purchases div:nth-child(3) p {
  text-align: end;
}

.Note {
  text-align: center;
  font-size: 13px;
  color: #7b8aa2;
  margin-top: 50px;
}

.FailedImage {
  width: 35%;
}
